<template>

    <div style="margin-right:20px;margin-left:10px;">
        <div class="q-row">
            <span>问题：
            <div class="btn-group" style="background-color: white;float: right;margin-right: 5px;">
                <button type="button" class="btn btn-Default" style="border-color: #ccc;background-color: white;height: 10px;line-height: 1px;font-size: 10px;" id="showSelectedIsAnswer">{{mData.isanswer}}</button>
                <button type="button" class="btn btn-Default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false" style="border-color: #ccc;background-color: white;height: 10px;">
                    <span class="caret" style="margin-top: -20px;"></span>
                    <span class="sr-only">Toggle Dropdown</span>
                </button>
                <ul class="dropdown-menu" style="min-width: 80px;font-size: 10px;">
                    <li><a @click="selectChangeIsAnswer(1)">必答</a></li>
                    <li role="separator" class="divider" style="margin: 3px 0;"></li>
                    <li><a @click="selectChangeIsAnswer(2)">非必答</a></li>
                </ul>
            </div>
            <textarea class="question" rows="2" v-model="mData.content"/></span>
            <span>备注<textarea class="question" rows="2" v-model="mData.ps" /></span>
        </div>


        <div v-if="mData.remark==0">
            <div class="q-row" v-for="(opt,index) in mData.options" :key="index">
                <span class="option-lb">第{{index+1}}选项.    </span>
                <input class="option" type="text" name="opt" v-model="opt.optText"/>
                <img class="del-btn" src="../assets/img/del.png" alt="删除" @click="deleteOpt(index)"/>
            </div>
        </div>
        <div v-else>
            <div class="q-row" v-for="(opt,index) in mData.options" :key="index">
                <div v-if="index+1==mData.options.length">
                    <span class="option-lb">其&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; 他.    </span>
                    <input class="option" type="text" name="opt"  disabled="disabled" v-model="opt.optText"/>
                    <img class="del-btn" src="../assets/img/del.png" alt="删除" @click="deleteQT(index)"/>
                </div>
                <div v-else>
                    <span class="option-lb">第{{index+1}}选项.    </span>
                    <input class="option" type="text" name="opt" v-model="opt.optText"/>
                    <img class="del-btn" src="../assets/img/del.png" alt="删除" @click="deleteOpt(index)"/>
                </div>
            </div>
        </div>
        <div class="q-row center">
            <button class="btn addOpt" @click="addOpt">添加选项</button>
            <button class="btn addOpt" @click="addOptQT">添加其他</button>
        </div>

        <hr>
        <div class="q-row" v-for="(opt,index) in mData.hideitems" :key="index">
            <div class="q-row" style="float: right;">
                <span class="option-lb" style="float:left;width:24%;margin-top: 5px;">选项号</span>
                <!-- <select @change="onParamChange(index)" v-model="mData.hideitems[index].optitem" class="form-control" style="width: 60%;float:left;height:28.35px;padding-top: 3px;">
                    <option value="">请选择选项</option>
                    <option value="1">第1选项</option><option value="2">第2选项</option><option value="3">第3选项</option><option value="4">第4选项</option>
                    <option value="5">第5选项</option><option value="6">第6选项</option><option value="7">第7选项</option><option value="8">第8选项</option>
                    <option value="9">第9选项</option><option value="10">第10选项</option><option value="11">第11选项</option><option value="12">第12选项</option>
                    <option value="13">第13选项</option><option value="14">第14选项</option><option value="15">第15选项</option><option value="16">第16选项</option>
                </select> -->
                <input class="option" @click="toChooseItems(index)" style="width: 60%;" type="text" name="opt" v-model="mData.hideitems[index].optitem" readonly/>
            </div>
            <div class="q-row" style="float: right;">
                <span class="option-lb" style="float:left;width:24%;margin-top: 5px;">隐藏题    </span>
                <input class="option" style="width: 60%;" type="number" name="opt" v-model="mData.hidenum[index].optitem"/>
                <img class="del-btn" src="../assets/img/del.png" alt="删除" @click="clearSelected(index)"/>
            </div>
        </div>


        <div class="q-row center">
            <button class="btn addOpt" style="margin-top: 3px;" @click="addOptHide">添加隐藏项</button>
        </div>

    </div>
</template>

<script>

    export default {
        name: "duoXuanEdit",
        props: ["editData"],
        data() {
            return {
                mData: this.$props.editData,
            }
        },
        methods: {
            deleteOpt: function (idx) {
                const temp = this.mData;
                temp.options.splice(idx, 1);
                this.$set(this.mData,temp);
            },
            addOpt: function () {
                const temp = this.mData;
                const idx = temp.options.length + 1;
                if( idx == 17){
                    alert("最多16个选项")
                }else{
                    if(temp.remark==1){
                        const temp = this.mData;
                        temp.options.splice(temp.options.length-1, 1);
                        this.$set(this.mData,temp);
                        const newidx = temp.options.length + 1;
                        temp.options.push({optText: "选项" + newidx});
                        this.$set(this.mData,temp);
                        temp.options.push({optText: "其他"});
                        this.$set(this.mData,temp);

                    }else{
                        temp.options.push({optText: "选项" + idx});
                        this.$set(this.mData,temp);
                    }
                }
            },
            addOptHide:function(){
                  const temp = this.mData;
                  const idx = temp.hideitems.length + 1;
                  temp.hideitems.push({optitem: ""});
                  temp.hidenum.push({optitem: ""});
                  /*if( idx >temp.options.length){
                      alert("当前只有"+temp.options.length+"个选项");
                  }else{
                      temp.hideitems.push({optitem: ""});
                      temp.hidenum.push({optitem: ""});
                  }*/
              },
            addOptQT:function(){
                const temp = this.mData;
                if(temp.remark==1){
                    alert("只能有一个其他选项");
                }else{
                    const idx = temp.options.length + 1;
                    if(idx == 17){
                        alert("最多16个选项")
                    }else{
                        temp.options.push({optText: "其他"});
                        temp.remark=1;
                        this.$set(this.mData,temp);
                     }
                }
            },
            deleteQT:function(idx){
                const temp = this.mData;
                temp.options.splice(idx, 1);
                temp.remark=0;
                this.$set(this.mData,temp);
            },
            selectChangeIsAnswer:function(obj){
                const temp = this.mData;
                if(obj==1){
                    temp.isanswer="必答";
                    $("#showSelectedIsAnswer").html("必答");
                }else{
                    temp.isanswer="非必答";
                    $("#showSelectedIsAnswer").html("非必答")
                 }

            },
            clearSelected:function(idx){
                  const temp = this.mData;
                  temp.hideitems.splice(idx, 1);
                  temp.hidenum.splice(idx, 1);
                  this.$set(this.mData,temp);
            },
            toChooseItems:function(idx){
                 //console.log(JSON.stringify(this.mData))
                 openwindow('/lnfwzl/lnkhzs/survey/dist/chooseOption.html?id='+this.mData.id+'&num='+idx+'&param='+JSON.stringify(this.mData.options));
                 //openwindow('http://localhost:8080/lnfwzl/lnkhzs/survey/chooseOptions.jsp?param='+JSON.stringify(this.mData.options));
             },
        }
    }
</script>

